.container{
    max-width: 1200px;
    margin: 0 auto;
}

.main{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.element{
    background-color: rgba(0, 0, 0, 0.76);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5px;
    width: 250px;
    height: 160px;
    margin: 5px;
}
.name h3{
    max-width: 180px;
    word-wrap: break-word;
    font-size: 18px;
}
.name span{
    padding-left: 10px;
}
.name{
    display: flex;
    justify-content: space-between;
    margin-bottom: 4px;
}
.hot{
    display: flex;
    flex-direction: column;
}
.hot r{
    text-decoration: underline;
    cursor: pointer;
}

.add{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.add a{
    color: black;
    text-decoration: none;
    padding: 5px;
    transition: all 0.3s ease;
}
.add_delete{
    background-color: #d95b43;
}
.add_vernyt{
    background-color: #53777a; /**          */
}
.add_ezm{
    background-color: #ecd078;
    display: none;
}
.add a:hover{
    opacity: 0.8;
}
.add_item{
    background-color: rgba(0, 0, 0, 0.76);
    padding: 5px;
    width: 250px;
    height: 160px;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 80px;
    cursor: pointer;
}
.add_item:hover{
    opacity: 0.8;
}

.mainAdd{
    background-color: rgba(0, 0, 0, 0.76);
    padding: 5px;

}
.formAdd{
    display: flex;
    flex-direction: column;
}

.formAdd span{
    font-size: 26px;
}

.formAdd_textArea{
    resize: none;
    background-color: transparent;
    border: 1px solid #fff;
    outline: none;
    font-size: 22px;
    color: #fff;
    margin-bottom: 5px;
}
.formAdd_input{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    font-size: 22px;
    color: #fff;
    margin-bottom: 5px;
}
.formAdd_box_con{
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.formAdd_box{
    margin-left: 30px;
    width: 20px;
    height: 20px;
}

.formAdd_photo{
    margin-left: 15px;
}

.formAdd_button{
    background-color: #ecd078;
    border: none;
    outline: none;
    font-size: 22px;
    color: #000;
    cursor: pointer;
    padding: 5px 0;
    margin-top: 10px;
}

.formAdd_number{
    margin-left: 30px;
    background-color: transparent;
    border: 1px solid #fff;
    outline: none;
    font-size: 22px;
    color: #fff;

}
.formAdd_numberDiv{
    position: relative;
}
.formAdd_numberDiv:hover::before{
    display: block;
}
.formAdd_numberDiv::before{
    content: 'от 1 до 5';
    position: absolute;
    display: none;
    color: #fff;
    padding: 3px;
    background-color: rgba(0, 0, 0, 0.45);
    top: -30px;
    left: 230px;
}


.main_change{
    background-color: rgba(0, 0, 0, 0.76);
    display: flex;
    flex-direction: column;
    padding: 5px;

}
.main_change_form span{
    font-size: 20px;
}

.change_new{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.change_new input{

}
.change_pop{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.change_ocenka{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.change_button {
    background-color: #ecd078;
    border: none;
    outline: none;
    font-size: 22px;
    color: #000;
    cursor: pointer;
    padding: 5px 30px;
    margin-top: 10px;

}
.panel{

}
.progress_1{
    position: absolute;
    display: block;
    top: 73px;
    height: 6px;
    width: 0;
    background-color: #d95b43;
    transition: all 1s ease;
}
.progress_2{
    position: absolute;
    display: block;
    top: 67px;
    height: 6px;
    width: 0;
    background-color: #c02942;
    transition: all 1s ease;
}












