.header{

    position: sticky;
    position: -webkit-sticky;
    top: 0;
    background-color: rgba(0,0,0,0.7);
    width: 100%;
    z-index: 10;
}

.header_main{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}

.header_papka{
    display: flex;
    align-items: center;
    outline: none;
}
.header_papka>a:first-child{
    color: #fff;
    text-decoration: none;
}

.logo{
    font-size: 28px;
    color: #fff;
}

.logo b{
    color: #ecd078;
}

.nav{
    padding-left: 30px;
}
.nav a{
    color: #fff;
    text-decoration: #fff;
}
.nav a:active{
    color: #ffffff;
}

.header_corzina{
    display: flex;
    align-items: center;
}

.auth{
    height: 28px;
    width: 28px;
    filter: invert(1);
    margin-right: 10px;
    cursor: pointer;
    outline: none;
}
.search{
    height: 32px;
    width: 32px;
    filter: invert(1);
    transition: all 0.3s ease;

}

.search_main{
    position: relative;
}

.search_con{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 17px;
    height: 17px;
    background-color: #d95b43;
    top: -5px;
    right: -5px;
    border-radius: 50%;
}

.search_count{
    color: #fff;
    font-size: 14px;
}


.search:hover{
    opacity: 0.8;
}


.user_menu:hover .auth{
    opacity: 0.8;
}

.user_menu{
    position: relative;
    outline: none;
}

.user_menu_check{
    position: absolute;
    display: block;
    height: 12px;
    width: 12px;
    background-color: #53777a;
    /*background-color: #542437;*/
    right: 12px;
    border-radius: 50%;
    top: -5px;
    z-index: 11;
}
/*.user_menu:hover .user_menu_hidden{
    display: flex!important;
}*/
.user_menu_hidden{
    position: absolute;
    right: 0;
    top: 48px;
    background-color: rgba(0,0,0,0.7);
    display: none;
    flex-direction: column;
    padding: 10px;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    text-align: right;
}
.user_menu_hidden a{
    color: white;
    text-decoration: none;
    transition: all 0.3s ease;
}
.user_menu_hidden a:hover{
    color: #ecd078;
}
.user_menu_hidden a:not(:last-child){
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #000;
}
.activeLink{
    color: #ecd078!important;
}

@media (max-width: 600px) {
    .nav{
        padding-left: 5px;
    }
}































