.Games{
    padding: 50px 0;

}

.Games_container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0px 10px;
}

.Games_main{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}
.item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    height: 100%;
}

.item_con{
    padding: 5px;

}
.item_a{
    -moz-appearance: none;
    -webkit-appearance: none;
    color: #fff;
    text-decoration: none;

}
.item_a h1{
    margin-bottom: 10px;
}

.item{
    display: inline-block;

    background-color: rgba(0, 0, 0, 0.76);
    width: 380px;
    margin-bottom: 30px;
}
.item img{
    object-fit: cover;
    width: 100%;

    /*width: 100%;
    height: 100%;*/
}

.item_fut{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:  10px 0 5px 0;
}
.item_fut span{
    font-size: 28px;
}
.item_fut_add{
    background-color: #ecd078;
}
.item_fut_remuve{
    background-color: #d95b43;
}
.item_fut a{
    color: #000;
    text-decoration: none;
    padding: 10px 15px;
    transition: all 0.3s ease;

}
.item_fut a:hover{
    opacity: 0.8;
}

@media (max-width: 1200px) {
    .Games_main{
        justify-content: space-around;
    }
}

.popularEl{
    position: absolute;
    top: 20px;
    right: -14px;
    background-color: #53777a;
    padding: 0 5px;

}
.novetlyEl{
    position: absolute;
    top: 50px;
    right: -14px;
    background-color: #542437;
    padding: 0 5px;
}
.card{
    position: absolute;
    top: 30px;
    right: -14px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.card_p{
    background-color: #53777a;
    padding: 0 5px;
    margin-bottom: 5px;
}
.card_n{
    background-color: #542437;
    padding: 0 5px;
}























