.basket{
    padding: 50px 0;
}

.basket_container{
    max-width: 1200px;
    margin: 0 auto;
}

.main{
    display: flex;
    flex-direction: column;
}

.item{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.73);

    margin-bottom: 10px;
}

.col_1{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none;
}
.col_1 h1{
    color: #fff;
    padding-left: 30px;
    max-width: 400px;
    word-wrap: break-word;
}

.col_2{
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.col_2 h1{

}

.col_2 a{
    color: #000;
    font-size: 26px;
    text-decoration: none;
    background-color: #ecd078 ;
    padding: 10px 15px;
    transition: all 0.3s ease;
}
.col_2 a:hover{
    opacity: 0.8;
}

.col_1>img{
    width: 200px;
    height: 200px;
}

.buy_con{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 30px;
}

.buy{
    text-align: center;
    color: white;
    font-size: 26px;
    text-decoration: none;
    background-color: #542437;
    max-width: 600px;
    width: 600px;
    padding: 10px 30px;
    transition: all 0.3s ease;
}
.buy:hover{
    background-color: #d95b43;
}
.buy_con p{
    font-size: 32px;
    background-color: rgba(0, 0, 0, 0.78);
    padding: 10px ;
}

.thank{
    background-color: rgba(0, 0, 0, 0.75);
    text-align: center;
    padding: 5px 0;
}
.thank a{
    color: #fff;
    font-size: 22px;
}

@media (max-width: 700px) {
    .item{
        flex-direction: row;
        justify-content: space-between;
    }
    .col_1{
        flex-direction: column;
        align-items: center;

    }
    .col_1 h1{
        padding-left: 0;
    }
    .col_1>img{
        width: 300px;
        height: 300px;
    }
}
@media (max-width: 550px) {
    .item{
        flex-direction: column;
        justify-content: center;
    }
    .col_1{
        flex-direction: column;
        align-items: center;

    }
    .col_1 h1{
        padding-left: 0;
    }
    .col_1>img{
        width: 100%;
        height: auto;
    }
    .col_2{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}































