.login_main{
    background-color: rgba(0, 0, 0, 0.74);
    display: flex;
    flex-direction: column;
    padding: 15px;

}
.login{
    display: flex;
    align-items: center;
    justify-content: center;
}
.login_main span{
    font-size: 28px;
}

.login_main h1{
    text-align: center;
}
.login_main input{
    width: 300px;
    font-size: 24px;
    background-color: transparent;
    border: none;
    border-radius: 0;
    outline: none;
    color: white;
    margin-bottom: 15px;
    margin-top: 5px;
    border: 1px solid #fff;
}

.log_button{
    font-size: 24px;
    padding: 5px 0;
    background-color: #ecd078;
    border: none;
    outline: none;
    margin-top: 15px;
    transition: all 0.3s ease;
}
.log_button:hover{
    opacity: 0.8;
}