.GamesId{
    padding: 50px 0;
}

.GamesId_container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0px 10px;
}

.main{
    background-color: rgba(0, 0, 0, 0.78);
}

.logoName{
    display: flex;
    flex-direction: row;
}

.logoName img{
    object-fit: cover;
    width: 50%;
}
.description{
    width: 80%;
    padding: 10px;
}
.description h1{
    margin-bottom: 15px;
}
.description p{
    margin-top: 15px;
}

.prise{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    padding: 10px;
}
.prise span{
    font-size: 32px;
}
.prise a{

    text-align: center;
    color: #000;
    font-size: 18px;
    text-decoration: none;
    background-color: #ecd078;
    padding: 10px 15px;
    transition: all 0.3s ease;

}
.prise_a_del{
    background-color: #d95b43!important;
    color: #fff!important;
}
.prise a:hover{
    opacity: 0.8;
}

.conMain{
    display: flex;
    flex-direction: row;
}

.star img{
    width: 50px;
    filter: invert(1);
}
.star img:not(:last-child){
    margin-right: 3px;
}

@media (max-width: 1000px) {
    .conMain{
        flex-direction: column;
        justify-content: space-between;
    }
    .prise{

        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;

    }
}

@media (max-width: 800px) {
    .logoName{
        flex-direction: column;
    }
    .logoName>img{
        object-fit: cover;
        width: 100%;
    }
    .GamesId{
        padding: 20px 0;
    }
}
















