.carousel_item{
    outline: none;
    height: 500px;

}

.carousel_item img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.Main_curosel{
    margin-top: 50px;
}

@media (max-width: 700px) {
    .carousel_item{
        height: 200px;
    }
}

.palka{
    margin: 50px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.palka_span_1{
    display: block;
    /*background-color: #ecd078;*/
    /*background-color: #542437;*/
    background-color: #53777a;
    height: 20px;
    width: 100%;
}
.palka_span_2{
    display: block;
    background-color: #c02942;
    height: 20px;
    width: 100%;
}

.palka h2{
    padding: 0 20px;
   /* background-color: rgba(0, 0, 0, 0.77);*/
    color: #53777a;
    font-size: 40px;
    background-color: #ecd078;
    text-transform: uppercase;
}
.palka h2 f{
    color: #c02942;

}

.Main_new_con{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-bottom: 30px;
}
.Main_new_con_item{
    background-color: rgba(0, 0, 0, 0.79);
    width: 100%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    height: 100%;
}
.Main_new_con_item img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.Main_new_con_item h1{
    text-align: center;
    font-size: 24px;
    word-wrap: break-word;
}
.Main_new_con_item_add{
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.Main_new_con_item_add span{
    font-size: 30px;
}
.Main_new_con_item_add_a{
    color: #000;
    text-decoration: none;
    background-color: #ecd078 ;
    padding: 10px 15px;
    transition: all 0.3s ease;
}
.Main_new_con_item_remyv_a:hover,
.Main_new_con_item_add_a:hover{
    opacity: 0.8;
}
.Main_new_con_item_remyv_a{
    color: #000;
    text-decoration: none;
    background-color: #d95b43 ;
    padding: 10px 15px;
    transition: all 0.3s ease;
}

.palka2{
    margin: 50px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.palka_span_3{
    display: block;
    /*background-color: #ecd078;*/
    /*background-color: #542437;*/
    background-color: #d95b43;
    height: 20px;
    width: 100%;
}
.palka_span_4{
    display: block;
    background-color: #542437;
    height: 20px;
    width: 100%;
}
.palka2 h2{
    padding: 0 20px;
    /* background-color: rgba(0, 0, 0, 0.77);*/
    color: #d95b43;
    font-size: 40px;
    background-color: #ecd078;
    text-transform: uppercase;
}
.palka2 h2 f{
    color: #542437;

}

.Main_navlink{
    color: #fff;
    text-decoration: none;

}

@media (max-width: 800px) {
    .Main_new_con{
        flex-direction: column;
        align-items: center;
    }

    .Main_new_con_item{
        width: 90%;
    }
    .Main_new_con_item h1{
        font-size: 36px;
    }
}











